	$: if (search.length != 0) {
		_privateCoaches= $privateCoaches.filter(element => {
			if (
				element.description.includes(search) 
				|| element.userWorker.user.lastname.includes(search)
				|| element.userWorker.user.name.includes(search)
				|| element.userWorker.user.phone.includes(search)
				|| element.userWorker.user.email.includes(search)
				|| element.userCustomer.user.lastname.includes(search)
				|| element.userCustomer.user.name.includes(search)
				|| element.userCustomer.user.phone.includes(search)
				|| element.userCustomer.user.email.includes(search)
			)
				return element;
		})
	} else {
		_privateCoaches = $privateCoaches;
	}
  async function removeCoach(id) {
   await api('DELETE', `${URL_DELETE_COACH}/${id}`, {}, false);
   privateCoaches.update(state => {
        let updated = state.filter(s => s.id != id)
        return updated
       })
  _privateCoaches = $privateCoaches;
  }
	async function getWorkers() {
		let _workers = await api(METHOD_GET, URL_WORKERS, {}, false);
