		coach = _coach;

	}
	async function getWeekdays() {
		let _weekdays = await api(METHOD_GET, URL_WEEKDAYS, null, false);
		weekdays.set(_weekdays)
	}
	function toggleAddCoach() {
		showAddCoach = !showAddCoach;
	}
	async	function addCoach() {
		toggleAddCoach();
		newCoach.start = `${newCoach.start} 00:00:00`;
		newCoach.end = `${newCoach.end} 00:00:00`;
		let _newCoach = await api(METHOD_POST, URL_NEW_COACH, newCoach, false);
		privateCoaches.update(state => {
			state.push(_newCoach);
			return state;
		});
	}
	async function getPrivateCoaches() {
		let response = await api(METHOD_GET, URL_GET_LIST, {}, false);
		privateCoaches.set(response);
	}
	async function deleteDay(i, id) {
		console.log("DAY", i, id)
		let response = await api('DELETE', `${URL_GROUP_WEEKDAY}/${id}`)
		console.log('ROUTE',`${URL_GROUP_WEEKDAY}/${id}`)
		let days = group.groupWeekdayses;
		days = days.splice(i, 1);
		group = group;

	}
	async function removeWeekday(id) {
			let response = await api('DELETE', `${URL_DELETE_WEEKDAY}/${id}`);
			let weekdays = coach.privateCoachWeekdayses;
			weekdays = weekdays.filter(weekday => weekday.id != id);
			coach.privateCoachWeekdayses = weekdays;
			coach = coach;
		}
	function selectCoach(id) {
		coach = $privateCoaches.filter(element => element.id == id);
		coach = coach[0];
		toggle();
	}
	function toggle() {
		show = !show;
	}
	onMount(() => {
		getWorkers();
		getWeekdays();
		getCustomers();
		getPrivateCoaches();
	})
</script>
