<script>
	import { api } from '../api.js';
	import { customers } from '../stores/index.js';
	import { onMount } from 'svelte';

	const URL = '/v1/usercustomer/list';
	const METHOD = 'GET';
	let customer;
	let show = false;
	let _customers;
	let search = '';
	$: if (search.length != 0) {
		_customers = $customers.filter(element => {
			if (
				element.user.name.includes(search) 
				|| element.user.lastname.includes(search)
				|| element.user.phone.includes(search)
				|| element.user.email.includes(search)
			)
				return element
		})
	} else {
		_customers = $customers
	}
	async function getCustomers() {
		let response = await api(METHOD, URL, {}, false);
		customers.set(response);
		console.log("RESPONSE ", response);
	}
	function selectCustomer(i) {
		customer = $customers.filter(element => element.id == i);
		customer = customer[0];
		toggle()
	}
	function toggle() {
		show = !show;
	}
	function toLocalTime(date) {
		let _date = new Date(date).toLocaleDateString()
    let time = new Date(date).toLocaleTimeString()
		return `${_date} ${time}`;
	}
	onMount(() => {
		getCustomers();
	})
</script>
{#if show}
	<div class="modal-backdrop fade show"></div>
	<div on:click={toggle} class="modal fade show" id="modal-form" tabindex="-1" aria-labelledby="modal-form" aria-modal="true" role="dialog" style="display: block; padding-left: 0px;">
	<div class="modal-dialog modal-dialog-centered" role="document">
		<div class="modal-content">
			<div class="modal-body p-0">
				<div class="card border-light p-4">
							<div class="card-header border-0 text-center pb-0">
									<h2 class="h4">Customer</h2>
							</div>
							<div class="card-body">
								<p>First name: {customer.user.name}</p>
								<p>Last name: {customer.user.lastname}</p>
								<p>Email: {customer.user.email}</p>
								<p>Phone: {customer.user.phone}</p>
								{#if customer.user.userType}
									<p>User type: {customer.user.userType.description}</p>
								{/if}
								{#if customer.customerMembershipFees && customer.customerMembershipFees.length != 0}
									<div class="card border-light shadow-sm mb-4">
										<div class="card-body">
												<div class="table-responsive">
														<table class="table table-centered table-nowrap mb-0 rounded">
																<thead class="thead-light">
																		<tr>
																				<th class="border-0">Amount</th>
																				<th class="border-0">Name</th>
																				<th class="border-0">Start date</th>
																				<th class="border-0">End date</th>
																		</tr>
																</thead>
																<tbody>
																	{#each customer.customerMembershipFees as membership}
																		<!-- Item -->
																		<tr>
																			<td class="border-0"><a href="#" class="text-primary font-weight-bold">{membership.membershipFeeType.amount}</a> </td>
																				<td class="border-0">
																					{membership.membershipFeeType.name}	
																				</td>
																				<td class="border-0">
																					{toLocalTime(membership.startDate)} 
																				</td>
																				<td class="border-0">
																					{toLocalTime(membership.endDate)}
																				</td>
																		</tr>
																	{/each}
																</tbody>
														</table>
												</div>
										</div>
										</div>
								{/if}
									<div class="d-block d-sm-flex justify-content-center align-items-center mt-4">
										<button class="btn btn-primary" on:click={toggle}>Close</button>
									</div>
							</div>
					</div>
				</div>
			</div>
		</div>
	</div>
{/if}
<div class="row p-0">
	<div class="col-6">
		<div class="input-group input-group-merge search-bar p-0">
				<span class="input-group-text" id="topbar-addon"><span class="fas fa-search"></span></span>
				<input type="text" class="form-control" id="topbarInputIconLeft" placeholder="Search" aria-label="Search" aria-describedby="topbar-addon" bind:value={search}>
		</div>
	</div>
</div>
<div class="card border-light shadow-sm mb-4 mt-4 p-0">
	<div class="card-body p-0">
			<div class="table-responsive">
					<table class="table table-centered table-nowrap mb-0 rounded">
							<thead class="thead-light">
									<tr>
											<th class="border-0">#</th>
											<th class="border-0">First name</th>
											<th class="border-0">Last name</th>
											<th class="border-0">Email</th>
											<th class="border-0">Phone</th>
											<th class="border-0">User type</th>
											<th class="border-0">Actions</th>
									</tr>
							</thead>
							<tbody>
								{#each _customers as customer, i}
									<!-- Item -->
									<tr>
										<td class="border-0"><a href="#" class="text-primary font-weight-bold">{customer.user.id}</a> </td>
											<td class="border-0 font-weight-bold"><span class="icon icon-xs icon-gray w-30"><span class="fas fa-globe-europe"></span></span>{customer.user.name}</td>
											<td class="border-0">
												{customer.user.lastname}	
											</td>
											<td class="border-0">
												{customer.user.email} 
											</td>
											<td class="border-0">
												{customer.user.phone}
											</td>
											<td class="border-0">
												{#if customer.user.userType}
													{customer.user.userType.description}
												{/if}
											</td>
											<td class="border-0">
												<button class="btn btn-primary" on:click={() => selectCustomer(customer.id)}>view</button>
											</td>
									</tr>
								{/each}
							</tbody>
					</table>
			</div>
	</div>
	</div>
