import { writable } from "svelte/store";

export const userWorker = writable({});
export const customers = writable([]);
export const activites = writable([]);
export const groups = writable([]);
export const privateCoaches = writable([]);
export const workers = writable([]);
export const workerRights = writable([]);
export const workerTypes = writable([]);
export const products = writable([]);
export const productTypes = writable([]);
export const productsTypes = writable([]);
export const weekdays = writable([]);

export const isLoggedIn = writable(false);

export function setPermissions(user) {
  if (!user.userWorker || user.userWorker.userWorkerWorkerRightses.length == 0)
    return;
  user.fullAccess = false;
  user.showWorkers = true;
  user.canAddCustomersToGroup = false;
  if (user.userWorker.userWorkerWorkerRightses.length != 0) {
    let workerRights = user.userWorker.userWorkerWorkerRightses;
    workerRights.forEach((el) => {
      if (
        el.workerRights.name == "Admin" ||
        el.workerRights.name == "Recepcija" ||
        el.workerRights.name == "Glavni Trener"
      )
        user.fullAccess = true;
      else if (
        el.workerRights.name != "Admin" ||
        el.workerRights.name != "Recepcija"
      )
        user.showWorkers = false;
      else if (
        el.workerRights.name == "Trener" ||
        el.workerRights.name == "Sporedna Recepcija"
      )
        user.canAddCustomersToGroup = true;
    });
  }
  // TODO remove user full access for deployment  <30-10-20, nikola> //
  // user.fullAccess = true;
  // user.showWorkers = true;
  return user;
}
