<script>
	import { api } from '../api.js';
	import { products, productTypes, userWorker } from '../stores/index.js';
	import { onMount } from 'svelte';

	const URL_GET_PRODUCTS = '/v1/product/list';
  const URL_REMOVE_PRODUCT = '/v1/product';
	const URL_GET_TYPES = '/v1/productType/list';
	const URL_CREATE_PRODUCT = '/v1/product';
	const URL_PRODUCT_DISCOUNT = '/v1/productDiscount';
	const METHOD_GET = 'GET';
	const METHOD_POST = 'POST';

	let _products;
	let search = '';
	let show = false;
	let showAddProduct = false;
	let disableAddProduct = true;
	let disableAddProductDiscount = true;

	$: (!newProductDiscount.name || !newProductDiscount.validFrom || !newProductDiscount.validTo || !newProductDiscount.discountPrice || !newProductDiscount.discount) ? disableAddProductDiscount = true : disableAddProductDiscount = false;
	$: (!newProduct.productType || !newProduct.name || !newProduct.basePrice || !newProduct.productPdv || !newProduct.description) ? disableAddProduct = true : disableAddProduct = false;
	$: if (search.length != 0 ) {
		_products = $products.filter(element => {
			if(element.name.toLowerCase().includes(search) 
				|| element.description.toLowerCase().includes(search) 
				|| element.productType.name.toLowerCase().includes(search)
			) 
			return element;
		}) } else {
			_products = $products;
		}	
	let product = {};
	let newProduct = {
		"productType": {}
	};
	let newProductDiscount = {
		"product": {
			"id": null
		}
	};
  async function removeProduct(id) {
   await api('DELETE', `${URL_REMOVE_PRODUCT}/${id}`, {}, false);
   products.update(state => {
        let updated = state.filter(s => s.id != id)
        return updated
       })
   _products = $products
  }
	async function removeDiscount(i, id) {
		let response = await api('DELETE', `${URL_PRODUCT_DISCOUNT}/${id}`)
		let discounts = product.productDiscounts;
		discounts = discounts.splice(i, 1);
		product = product;
	}
	async function addProductDiscount() {
		newProductDiscount.validFrom = `${newProductDiscount.validFrom} 00:00:00`;
		newProductDiscount.validTo = `${newProductDiscount.validTo} 00:00:00`;
		newProductDiscount.product.id = product.id;
		let _product = await api(METHOD_POST, URL_PRODUCT_DISCOUNT, newProductDiscount, false);
		product = _product;
		newProductDiscount = { "product": { "id": null } };
	}
	async function addProduct() {
		newProduct.priceWithPdv = parseFloat(parseInt(newProduct.basePrice) * (1 + (parseInt(newProduct.productPdv) / 100)));
		let _newProduct = await api(METHOD_POST, URL_CREATE_PRODUCT, newProduct, false);
		products.update(state => {
			state.push(_newProduct);
			return state;
		});
		toggleAddProduct();
	}
	function toggleAddProduct() {
		showAddProduct = !showAddProduct;	
	}
	async function getProducts() {
		let response = await api(METHOD_GET, URL_GET_PRODUCTS, {}, false);
		products.set(response);
		console.log("RESPONSE ", response);
	}
	async function getProductTypes() {
		let response = await api(METHOD_GET, URL_GET_TYPES, {}, false);
		productTypes.set(response);
		console.log("RESPONSE ", response);
	}
	function selectProduct(id) {
		product = $products.filter(element => element.id == id);
		product = product[0];
		toggle()
	}
	function toggle() {
		show = !show;
	}
	function toLocalTime(date) {
		let _date = new Date(date).toLocaleDateString()
    let time = new Date(date).toLocaleTimeString()
		return `${_date} ${time}`;
	}
	onMount(() => {
		getProducts();
		getProductTypes();
	})
</script>
{#if show && $userWorker.fullAccess}
	<div class="modal-backdrop fade show"></div>
	<div class="modal fade show" id="modal-form" tabindex="-1" aria-labelledby="modal-form" aria-modal="true" role="dialog" style="display: block; padding-left: 0px;">
	<div class="modal-dialog modal-dialog-centered modal-lg" role="document">
		<div class="modal-content">
			<div class="modal-body modal-scrollable p-0" style="height: calc(100vh - 40px); overflow: scroll;">
				<div class="card border-light p-4">
							<div class="card-header border-0 text-center pb-0">
									<h2 class="h4">Product</h2>
							</div>
							<div class="card-body">
								<div class="row">
									<div class="col-6">
										<p>Name: {product.name}</p>
										<p>Base price: {product.basePrice}</p>
										<p>Pdv: {product.productPdv}</p>
										<p>Price with PDV: {product.priceWithPdv}</p>
										<p>Description: {product.description}</p>
										<p>Product type: {product.productType.name}</p>
									</div>
									<div class="col-6">
										<div class="form-group mb-4">
												<div class="input-group">
													<input type="text" class="form-control" placeholder="name" id="exampleInputEmailCard1" bind:value={newProductDiscount.name}>
												</div>  
										</div>
										<div class="form-group mb-4">
												<div class="input-group">
													<input type="text" class="form-control" placeholder="discount price" id="exampleInputEmailCard1" bind:value={newProductDiscount.discountPrice}>
												</div>  
										</div>
										<div class="form-group mb-4">
												<div class="input-group">
													<input type="text" class="form-control" placeholder="%" id="exampleInputEmailCard1" bind:value={newProductDiscount.discount}>
												</div>  
										</div>
										<div class="form-group mb-4">
												<label for="">From</label>
												<div class="input-group">
													<input type="date" step="1" class="form-control" placeholder="from" id="exampleInputEmailCard1" bind:value={newProductDiscount.validFrom}>
												</div>  
										</div>
										<div class="form-group mb-4">
												<label for="">To</label>
												<div class="input-group">
													<input type="date" step="1" class="form-control" placeholder="to" id="exampleInputEmailCard1" bind:value={newProductDiscount.validTo}>
												</div>  
										</div>
										<div class="form-group mb-4">
											<button class="btn btn-primary btn-block" on:click={addProductDiscount} disabled={disableAddProductDiscount}>Add</button>
										</div>
									</div>
								</div>
								{#if product.productDiscounts && product.productDiscounts.length != 0}
									<div class="row">
										<div class="col-12">
													<div class="table-responsive">
															<table class="table table-centered table-nowrap mb-0 rounded">
																	<thead class="thead-light">
																		<tr>
																			<th class="border-0">Actions</th>
																			<th class="border-0">Name</th>
																			<th class="border-0">Discount percent</th>
																			<th class="border-0">Discount price</th>
																			<th class="border-0">Valid from</th>
																			<th class="border-0">Valid to</th>
																		</tr>
																	</thead>
																	<tbody>
																		{#each product.productDiscounts as productDiscount, i}
																			<!-- Item -->
																			<tr>
																				<td class="border-0">
																					<button class="btn btn-danger" on:click={() => removeDiscount(i, productDiscount.id)}>del</button>
																				</td>
																				<td class="border-0"><a class="text-primary font-weight-bold">{productDiscount.name}</a> </td>
																					<td class="border-0">
																						{productDiscount.discount}	
																					</td>
																					<td class="border-0">
																						{productDiscount.discountPrice} 
																					</td>
																					<td class="border-0">
																						{productDiscount.validFrom} 
																					</td>
																					<td class="border-0">
																						{productDiscount.validTo} 
																					</td>
																			</tr>
																		{/each}
																	</tbody>
															</table>
													</div>
										</div>
									</div>
								{/if}
							</div>
							<div class="d-block d-sm-flex justify-content-center align-items-center mt-4">
								<button class="btn btn-primary" on:click={toggle}>Close</button>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
{/if}
{#if showAddProduct && $userWorker.fullAccess}
<div class="modal-backdrop fade show"></div>
<div class="modal fade show" id="modal-form" tabindex="-1" role="dialog" aria-labelledby="modal-form" aria-modal="true" style="display: block;">
		<div class="modal-dialog modal-dialog-centered" role="document">
				<div class="modal-content">
						<div class="modal-body p-0">
								<div class="card border-light p-4">
										<div class="card-header border-0 text-center pb-0">
												<h2 class="h4">Add product</h2>
										</div>
										<div class="card-body">
											<!-- Form -->
											<div class="form-group mb-4">
												<label>Product type</label>
												<select bind:value={newProduct.productType.id} class="form-select mb-4" id="country" aria-label="Default select example">
													<option selected>select type</option>
													{#each $productTypes as type}
														<option value={type.id}>{type.name}</option>
													{/each}
												</select>
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Name</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="name" id="exampleInputEmailCard1" bind:value={newProduct.name}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Base price</label>
													<div class="input-group">
														<input type="number" class="form-control" placeholder="base price" id="exampleInputEmailCard1" bind:value={newProduct.basePrice} />
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Pdv</label>
													<div class="input-group">
														<input type="number" class="form-control" placeholder="pdv" id="exampleInputEmailCard1" bind:value={newProduct.productPdv}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Description</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="description" id="exampleInputEmailCard1" bind:value={newProduct.description}>
													</div>  
											</div>
											<div class="d-block d-sm-flex justify-content-center align-items-center mt-4">
												<button class="btn btn-primary" on:click={addProduct} disabled={disableAddProduct} >Add</button>
												<button class="btn btn-primary ml-4" on:click={toggleAddProduct}>Close</button>
											</div>
										</div>
								</div>
						</div>
				</div>
		</div>
</div>

{/if}
<div class="row p-0 mb-4">
	<div class="col-8">
		<div class="input-group input-group-merge search-bar p-0">
				<span class="input-group-text" id="topbar-addon"><span class="fas fa-search"></span></span>
				<input type="text" class="form-control" id="topbarInputIconLeft" placeholder="Search" aria-label="Search" aria-describedby="topbar-addon" bind:value={search}>
		</div>
	</div>
	{#if $userWorker.fullAccess}
		<div class="col-4">
			<button class="btn btn-primary" on:click={toggleAddProduct}>Add product</button>
		</div>
	{/if}
</div>
<div class="card border-light shadow-sm mb-4 p-0">
	<div class="card-body p-0">
			<div class="table-responsive">
					<table class="table table-centered table-nowrap mb-0 rounded">
							<thead class="thead-light">
									<tr>
											<th class="border-0">#</th>
											<th class="border-0">Name</th>
											<th class="border-0">Base price</th>
											<th class="border-0">PDV</th>
											<th class="border-0">Price with PDV</th>
											<th class="border-0">Description</th>
											<th class="border-0">Product type</th>
											{#if $userWorker.fullAccess}
												<th class="border-0">Actions</th>
											{/if}
									</tr>
							</thead>
							<tbody>
								{#each _products as product, i}
									<!-- Item -->
									<tr>
										<td class="border-0"><a href="#" class="text-primary font-weight-bold">{product.id}</a> </td>
											<td class="border-0 font-weight-bold"><span class="icon icon-xs icon-gray w-30"><span class="fas fa-globe-europe"></span></span>{product.name}</td>
											<td class="border-0 font-weight-bold"><span class="icon icon-xs icon-gray w-30"><span class="fas fa-globe-europe"></span></span>{product.basePrice}</td>
											<td class="border-0">
												{product.productPdv}	
											</td>
											<td class="border-0">
												{product.priceWithPdv} 
											</td>
											<td class="border-0">
												{product.description}
											</td>
											<td class="border-0">
												{product.productType.name}
											</td>
											{#if $userWorker.fullAccess}
												<td class="border-0">
													<button class="btn btn-primary" on:click={() => selectProduct(product.id)}>view</button>
													<button class="btn btn-danger" on:click={() => removeProduct(product.id)}>del</button>
												</td>
											{/if}
									</tr>
								{/each}
							</tbody>
					</table>
			</div>
	</div>
	</div>
