<script>
	import { api } from '../api.js';
	import { workers, workerRights, workerTypes, userWorker } from '../stores/index.js';
	import { onMount } from 'svelte';

	const URL = '/v1/user/workerlist';
	const URL_WORKER_RIGHTS = '/v1/userRights/list';
  const URL_ADD_WORKER_RIGHT = '/v1/userworkerrights';
  const URL_ADD_WORKER = '/v1/userWorker/create';
  const URL_WORKER_TYPELIST = '/v1/user/workertypelist';
	const URL_DELETE_WORKER_RIGHT = '/v1/userworkerrights';
  const URL_UPDATE_CREDENTIALS = '/v1/user/updateCredentials';
	const METHOD = 'GET';
	const METHOD_POST = 'POST';

	let worker;
	let show = false;
	let _workers;
	let search = '';
	let showAddWorker = false;
  let newWorkerRights = {
    "userWorker": {
        "id": null
      },
    "workerRights": {
        "id": null
      }
    }
	let newWorker = {
      "user": {
        "address": {}
      },
      "workerType":{
        "id": null
      }
		}
  let newCredentials = {}
	let canCreateWorker = false;
		$: if (newWorker.workerType.id && newWorker.user.name && newWorker.user.lastname &&
				newWorker.user.email && newWorker.user.phone && newWorker.user.address.addressLine
				&& newWorker.user.address.city
				&& newWorker.user.address.country
				&& newWorker.user.address.zip
			) { 
				canCreateWorker = true
			} else { canCreateWorker = false; }
	$: if (search.length != 0) {
		_workers = $workers.filter(element => {
        console.log('Elemenr', element);
        search = search.toLowerCase();
        let {name, lastname, phone, email} = element;
			if (
				name.toLowerCase().includes(search) 
				|| lastname.toLowerCase().includes(search)
				|| phone.toLowerCase().includes(search)
				|| email.toLowerCase().includes(search)
			)
					return element;
		})
	} else {
			_workers = $workers; 
	}
  async function changeCredentials() {
    newCredentials.id = worker.id;
    await api(METHOD_POST, URL_UPDATE_CREDENTIALS, newCredentials, false);
    newCredentials = {};
    alert('Username and password updated');
  }
	async function removeWorkerRight(i, id) {
		let _worker = await api('DELETE', `${URL_DELETE_WORKER_RIGHT}/${id}`, {}, false);
		let rights = worker.userWorker.userWorkerWorkerRightses;
		rights = rights.splice(i, 1);
		worker = worker;
	}
	async function addWorkerRights() {
			newWorkerRights.userWorker.id = worker.id;
			let _worker = await api(METHOD_POST, URL_ADD_WORKER_RIGHT, newWorkerRights, false);
			worker = _worker;
		}
	async function getWorkerTypes() {
			let response = await api(METHOD, URL_WORKER_TYPELIST, {}, false);
			workerTypes.set(response);
		}
	async function addWorker() {
			let worker = await api(METHOD_POST, URL_ADD_WORKER, newWorker, false);
			workers.update(state => {
					state.push(worker);
					return state;
				})
			_workers = $workers;
			toggleAddWorker();
		}
	function toggleAddWorker() {
			showAddWorker = !showAddWorker
		}
	async function getWorkerRights() {
			let response = await api(METHOD, URL_WORKER_RIGHTS, {}, false);
			workerRights.set(response);
		}
	async function getWorkers() {
		let response = await api(METHOD, URL, {}, false);
		workers.set(response);
	}
	function selectWorker(i) {
		worker = $workers.filter(element => element.id == i);
		worker = worker[0];
		toggle()
	}
	function toggle() {
		show = !show;
	}
	function toLocalTime(date) {
		let _date = new Date(date).toLocaleDateString()
    let time = new Date(date).toLocaleTimeString()
		return `${_date} ${time}`;
	}
	onMount(() => {
		getWorkerTypes();
    getWorkerRights();
		getWorkers();
	})
</script>
{#if showAddWorker && $userWorker.fullAccess}
<div class="modal-backdrop fade show"></div>
<div class="modal fade show" id="modal-form" tabindex="-1" role="dialog" aria-labelledby="modal-form" aria-modal="true" style="display: block;">
		<div class="modal-dialog modal-dialog-centered modal-scrollable" role="document">
			<div class="modal-content" style="height: calc(100vh - 60px); overflow: scroll;">
						<div class="modal-body p-0">
								<div class="card border-light p-4">
										<div class="card-header border-0 text-center pb-0">
												<h2 class="h4">Add worker</h2>
										</div>
										<div class="card-body">
											<!-- Form -->
                      <div class="form-group mb-4">
                        <select bind:value={newWorker.workerType.id} class="form-select" id="country" aria-label="Default select example">
                          <option selected>select type</option>
                          {#each $workerTypes as type}
                            <option value={type.id}>{type.name}</option>
                          {/each}
                        </select>
                      </div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Username</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="username" id="exampleInputEmailCard1" bind:value={newWorker.user.username}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Password</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="password" id="exampleInputEmailCard1" bind:value={newWorker.user.password}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Name</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="name" id="exampleInputEmailCard1" bind:value={newWorker.user.name}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Last name</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="last name" id="exampleInputEmailCard1" bind:value={newWorker.user.lastname} />
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Email</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="email" id="exampleInputEmailCard1" bind:value={newWorker.user.email}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Phone</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="phone" id="exampleInputEmailCard1" bind:value={newWorker.user.phone}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Address</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="address" id="exampleInputEmailCard1" bind:value={newWorker.user.address.addressLine}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">City</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="city" id="exampleInputEmailCard1" bind:value={newWorker.user.address.city}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Country</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="country" id="exampleInputEmailCard1" bind:value={newWorker.user.address.country}>
													</div>  
											</div>
											<div class="form-group mb-4">
													<label for="exampleInputEmailCard1">Zip</label>
													<div class="input-group">
														<input type="text" class="form-control" placeholder="zip" id="exampleInputEmailCard1" bind:value={newWorker.user.address.zip}>
													</div>  
											</div>
											<div class="d-block d-sm-flex justify-content-center align-items-center mt-4">
												<button class="btn btn-primary" disabled={!canCreateWorker} on:click={addWorker}>Add</button>
												<button class="btn btn-primary ml-4" on:click={toggleAddWorker}>Close</button>
											</div>
										</div>
								</div>
						</div>
				</div>
		</div>
</div>
{/if}
{#if show && $userWorker.fullAccess}
	<div class="modal-backdrop fade show"></div>
	<div class="modal fade show" id="modal-form" tabindex="-1" aria-labelledby="modal-form" aria-modal="true" role="dialog" style="display: block; padding-left: 0px;">
	<div class="modal-dialog modal-dialog-centered modal-scrollable modal-lg" role="document">
		<div class="modal-content">
			<div class="modal-body p-0" style="height: calc(100vh - 60px); overflow: scroll;">
				<div class="card border-light p-4">
							<div class="card-header border-0 text-center pb-0">
									<h2 class="h4">Worker</h2>
							</div>
							<div class="card-body">
								<div class="row">
									<div class="col-6">
										<p>First name: {worker.name}</p>
										<p>Last name: {worker.lastname}</p>
										<p>Email: {worker.email}</p>
										<p>Phone: {worker.phone}</p>
										{#if worker.userType}
											<p>User type: {worker.userType.description}</p>
										{/if}
									</div>
									<div class="col-6 mb-4">
										<div class="form-group mb-4">
												<label for="exampleInputEmailCard1">Username</label>
												<div class="input-group">
													<input type="text" class="form-control" placeholder="username" id="exampleInputEmailCard1" bind:value={newCredentials.username}>
												</div>  
										</div>
										<div class="form-group">
												<label for="exampleInputEmailCard1">Password</label>
												<div class="input-group">
													<input type="text" class="form-control" placeholder="password" id="exampleInputEmailCard1" bind:value={newCredentials.password}>
												</div>  
										</div>
                    <button class="btn btn-primary btn-block mb-4 mt-2" disabled={!newCredentials.username || !newCredentials.password} on:click={changeCredentials}>Change</button>
										<select bind:value={newWorkerRights.workerRights.id} class="form-select" id="country" aria-label="Default select example">
											<option selected>select rights</option>
											{#each $workerRights as right}
												<option value={right.id}>{right.name}</option>
											{/each}
										</select>
										<button class="btn btn-primary btn-block mt-2" on:click={addWorkerRights}>Add</button>
									</div>
								</div>
								{#if worker.userWorker.userWorkerWorkerRightses && worker.userWorker.userWorkerWorkerRightses.length != 0}
									<div class="card border-light shadow-sm mb-4 p-0">
										<div class="card-body p-0">
												<div class="table-responsive">
														<table class="table table-centered table-nowrap mb-0 rounded">
																<thead class="thead-light">
																		<tr>
																				<th class="border-0">Actions</th>
																				<th class="border-0">Id</th>
																				<th class="border-0">Name</th>
																		</tr>
																</thead>
																<tbody>
																	{#each worker.userWorker.userWorkerWorkerRightses as rights, i}
																		<tr>
																			<td class="border-0">
																				<button class="btn btn-danger" on:click={() => removeWorkerRight(i, rights.id)}>del</button>
																			</td>
																			<td class="border-0">
																				{rights.id}	
																			</td>
																			<td class="border-0">
																				{rights.workerRights.name} 
																			</td>
																		</tr>
																	{/each}
																</tbody>
														</table>
												</div>
										</div>
										</div>
								{/if}
									<div class="d-block d-sm-flex justify-content-center align-items-center mt-4">
										<button class="btn btn-primary" on:click={toggle}>Close</button>
									</div>
							</div>
					</div>
				</div>
			</div>
		</div>
	</div>
{/if}
<div class="row p-0">
	<div class="col-6">
		<div class="input-group input-group-merge search-bar p-0">
				<span class="input-group-text" id="topbar-addon"><span class="fas fa-search"></span></span>
				<input type="text" class="form-control" id="topbarInputIconLeft" placeholder="Search" aria-label="Search" aria-describedby="topbar-addon" bind:value={search}>
		</div>
	</div>
	{#if $userWorker.fullAccess}
		<div class="col-6">
			<button class="btn btn-primary" on:click={toggleAddWorker}>Add worker</button>
		</div>
	{/if}
</div>
<div class="card border-light shadow-sm mb-4 mt-4 p-0">
	<div class="card-body p-0">
			<div class="table-responsive">
					<table class="table table-centered table-nowrap mb-0 rounded">
							<thead class="thead-light">
									<tr>
											<th class="border-0">#</th>
											<th class="border-0">First name</th>
											<th class="border-0">Last name</th>
											<th class="border-0">Email</th>
											<th class="border-0">Phone</th>
											<th class="border-0">Worker type</th>
											<th class="border-0">Actions</th>
									</tr>
							</thead>
							<tbody>
								{#each _workers as worker, i}
									<!-- Item -->
									<tr>
										<td class="border-0"><a href="#" class="text-primary font-weight-bold">{worker.id}</a> </td>
											<td class="border-0 font-weight-bold"><span class="icon icon-xs icon-gray w-30"><span class="fas fa-globe-europe"></span></span>{worker.name}</td>
											<td class="border-0">
												{worker.lastname}	
											</td>
											<td class="border-0">
												{worker.email} 
											</td>
											<td class="border-0">
												{worker.phone}
											</td>
											<td class="border-0">
												{#if worker.userWorker && worker.userWorker.workerType }
													{worker.userWorker.workerType.name}
												{/if}
											</td>
											{#if $userWorker.fullAccess}
												<td class="border-0">
													<button class="btn btn-primary" on:click={() => selectWorker(worker.id)}>view</button>
												</td>
											{/if}
									</tr>
								{/each}
							</tbody>
					</table>
			</div>
	</div>
	</div>
