<script>
	import {link} from 'svelte-spa-router'
	import ChildLink from './ChildLink.svelte';
	import { userWorker } from '../stores/index.js';

	export let _link;
	let show=false;
	function toggle() {
		show = !show;
	}
</script>
{#if _link.children}
	<li class="nav-item" on:click={toggle} style="{$userWorker.canSeeWorkers ? '' : 'display:none;'}">
		<span class="nav-link {show ? '' : 'colapsed'} d-flex justify-content-between align-items-center" data-toggle="collapse" data-target="#submenu-app">
			<span>
				<span class="sidebar-icon"><span class="fas fa-{_link.icon}"></span></span> 
				{_link.name}	
			</span>
			<span class="link-arrow"><span class="fas fa-chevron-right"></span></span> 
		</span>
	</li>
	{#each _link.children as child}
		<ChildLink link={child} {show} />
	{/each}
{:else}
	<li class="nav-item" on:click={toggle} style="{!$userWorker.showWorkers && _link.name == 'Workers' ? 'display:none;' : ''}">
		<a href='/dashboard/{_link.route}' use:link class="nav-link">
			<span class="sidebar-icon"><span class="fas fa-{_link.icon}"></span></span>
			<span>{_link.name}</span>
		</a>
	</li>
{/if}


