import App from "./App.svelte";
import { api } from "./api.js";
import { push } from "svelte-spa-router";
import { userWorker, isLoggedIn, setPermissions } from "./stores/index.js";
const METHOD = "POST";
const URL = "/v1/userworker/login";

async function checkCookies() {
  if (Cookies.get("worker")) {
    let { username, password } = JSON.parse(Cookies.get("worker"));
    let worker = await api(
      METHOD,
      URL,
      {
        username: username,
        password: password,
      },
      false
    );
    userWorker.set(setPermissions(worker));
    isLoggedIn.set(true);
    startApp();
    push("/dashboard/customers");
  } else {
    startApp();
  }
}

checkCookies();
async function startApp() {
  const app = new App({
    target: document.body,
  });
}

export default app;
