<script>
	import { push } from "svelte-spa-router";
	import { userWorker, isLoggedIn } from "../stores/index.js";
	function logout() {
		if (Cookies.get('worker')) Cookies.remove('worker');
		isLoggedIn.set(false);
		push('/');
	}
</script>
<ul class="navbar-nav align-items-center mb-4">
	<li class="nav-item dropdown">
	</li>
	<li class="nav-item dropdown">
		<a class="nav-link pt-1 px-0" href="#" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
			<div class="media d-flex align-items-center">
				<div class="media-body ml-2 text-dark align-items-center d-none d-lg-block">
					<p class="mb-0 font-small font-weight-bold">{$userWorker.name} {$userWorker.lastname}</p>
				</div>
				<button class="btn btn-danger ml-4" on:click={logout}>Logout</button>
			</div>
		</a>
	</li>
</ul>
