import fetch from "unfetch";
import { stringify } from "qs";
// import { loading, placeParams, key } from '../stores/index.js'

export let api = async (method, url, params, disableLoading) => {
  method = method.toUpperCase();

  let data = {};

  if (params) data = { ...data, ...params };

  let options = {
    method: method,
    headers: {
      "Content-Type": "application/json",
    },
  };
  if (method === "GET") {
    url = prepareURL(url, data);
  }
  if (method === "POST") {
    options.body = JSON.stringify(data);
  }

  let host = `https://gym17.herokuapp.com${url}`;
  if (url.includes(":3102")) host = url;
  let response = await fetch(host, options);
  return checkStatus(response);
};

function prepareURL(url, data) {
  return (
    `${url}?` +
    Object.keys(data)
      .map((key) => `${key}=${data[key]}`)
      .join("&")
  );
}

function checkStatus(response) {
  if (response.ok) {
    return response.json();
  } else {
    console.log("ERROR", response);
    let error = new Error(response.statusText);
    error.response = response;
    return Promise.reject(error);
  }
}
