
<script>
	import { api } from '../api.js';
	import { groups, customers, workers, weekdays, userWorker } from '../stores/index.js';
	import { onMount } from 'svelte';

	const URL_GROUP = '/v1/group/list';
	const URL_NEW_GROUP = '/v1/group';
	const URL_DELETE_GROUP = '/v1/group';
	const URL_CUSTOMERS = '/v1/usercustomer/list';
	const URL_WORKERS = '/v1/userworkerCoach/list';
	const URL_WEEKDAYS = '/v1/weekdays/list';
	const URL_GROUP_CUSTOMER = '/v1/groupCustomer';
	const URL_GROUP_WORKER = '/v1/groupWorker';
	const URL_GROUP_WEEKDAY = '/v1/groupWeekdays';
	const METHOD_GET = 'GET';
	const METHOD_POST = 'POST';

	let customerSelectedId;
	let _groups = [];
	let search = '';
	let weekdaySelectedId;
	let hour;
	let workerSelectedId;
	let group;
	let newGroup = {
		"active": true,
		"description": "",
		"maxCustomer": 0,
		"name": "",
		"start": "",
	}
	let show = false;
	let showAddGroup = false;
	let enableCreateGroup = false;
	$: !newGroup.name || !newGroup.description || !newGroup.maxCustomer || !newGroup.start ? enableCreateGroup = false : enableCreateGroup = true;
	$: if (search.length != 0) {
		_groups = groups.filter(element => {
			if (
				element.name.includes(search) 
				|| element.description.includes(search)
			)
				return element;
		})
	} else {
		_groups = $groups;
	}

	function toggle() {
		show = !show;
	}
	function toggleAddGroup() {
		showAddGroup = !showAddGroup;
	}

	function toLocalTime(date) {
    let time = new Date(date).toLocaleTimeString()
		return `${time}`;
	}
async function deleteGroup(id) {
  await api('DELETE', `${URL_DELETE_GROUP}/${id}`, {}, false);
  groups.update(state => {
      let updated = state.filter(s => s.id != id);
      return updated;
      })
  _groups = $groups;
}
	async function getWorkers() {
		let _workers = await api(METHOD_GET, URL_WORKERS, {}, false);
		workers.set(_workers);
		console.log("WORKERS", _workers);
	}

	async	function addGroup() {
		toggleAddGroup();
		newGroup.start = `${newGroup.start} 00:00:00`;
		let _newGroup = await api(METHOD_POST, URL_NEW_GROUP, newGroup, false);
		groups.update(state => {
			state.push(_newGroup);
			return state;
		});
	}
	async function getGroups() {
		let _groups = await api(METHOD_GET, URL_GROUP, {}, false);
		groups.set(_groups);
	}
	function selectGroup(id) {
		group = $groups.filter(element => element.id == id);
		group = group[0];
		toggle();
	}

	async function addGroupCustomer() {
		let data = {
			"group": {
				"id": group.id
			},
			"userCustomer": {
				"id": customerSelectedId
			}
		}
		let _group = await api(METHOD_POST, URL_GROUP_CUSTOMER, data, false); 
		group = _group;
		
	}
	async function addGroupWeekday() {
		let data = {
			"group": {
				"id": group.id
			},
			"weekdays": {
				"id": weekdaySelectedId
			},
			"hour": hour,
			"duration": 1
		};
		let _group = await api(METHOD_POST, URL_GROUP_WEEKDAY, data, false);
		group = _group;

	}
	async function addGroupWorker() {
		let data = {
			"group": {
				"id": group.id
			},
			"userWorker": {
				"id": workerSelectedId
			}
		}
		let _group = await api(METHOD_POST, URL_GROUP_WORKER, data, false);
		group = _group;
	}
	async function getCustomers() {
		let _customers = await api(METHOD_GET, URL_CUSTOMERS, {}, false);
		customers.set(_customers);
	}

	async function deleteDay(i, id) {
		let response = await api('DELETE', `${URL_GROUP_WEEKDAY}/${id}`)
		let days = group.groupWeekdayses;
		days = days.splice(i, 1);
		group = group;

	}

	async function removeCustomer(i, id) {
		let response = await api('DELETE', `${URL_GROUP_CUSTOMER}/${id}`)
		let customers = group.groupCustomers;
		customers = customers.splice(i, 1);
		group = group;
	}
	async function removeWorker(i, id) {
		let response = await api('DELETE', `${URL_GROUP_WORKER}/${id}`)
		let workers = group.groupWorkers;
		workers = workers.splice(i, 1);
		group = group;

	}
	async function getWeekdays() {
		let _weekdays = await api(METHOD_GET, URL_WEEKDAYS, null, false);
		weekdays.set(_weekdays)
	}
	onMount(() => {
		getGroups();
		getCustomers();
		getWorkers();
		getWeekdays();
	})
</script>
{#if show}
	<div class="modal-backdrop fade show"></div>
	<div class="modal fade show" id="modal-form" tabindex="-1" aria-labelledby="modal-form" aria-modal="true" role="dialog" style="display: block; padding-left: 0px;">
	<div class="modal-dialog modal-dialog-scrollable modal-dialog-centered modal-lg" role="document">
		<div class="modal-content">
			<div class="modal-body p-0" style="height: calc(100vh - 20px);">
				<div class="card border-light p-4">
							<div class="card-header border-0 text-center pb-0">
									<h2 class="h4">Group</h2>
							</div>
							<div class="card-body">
								<div class="row mb-4">
									<div class="col-6">
										<p>Name: {group.name}</p>
										<p>Description: {group.description}</p>
										<p>Start: {group.start}</p>
										<p>Max customers: {group.maxCustomer}</p>
									</div>
									<div class="col-6 flex-nowrap">
										<div class="row">
											<div class="col-10">
												<select bind:value={customerSelectedId} class="form-select" id="country" aria-label="Default select example">
													<option selected>add customer</option>
													{#each $customers as customer}
														<option value={customer.id}>{customer.user.name} {customer.user.lastname}</option>
													{/each}
												</select>
											</div>
											<div class="col-2">
												<button on:click={addGroupCustomer} class="btn btn-primary mb-4 dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
													<span class="fas fa-plus "></span>
												</button>
											</div>
										</div>
                    {#if $userWorker.fullAccess}
                      <div class="row">
                        <div class="col-10">
                          <select bind:value={workerSelectedId} class="form-select" id="country" aria-label="Default select example">
                            <option selected>add worker</option>
                            {#each $workers as worker}
                              <option value={worker.id}>{worker.user.name} {worker.user.lastname}</option>
                            {/each}
                          </select>
                        </div>
                        <div class="col-2">
                          <button on:click={addGroupWorker} class="btn btn-primary mb-4 dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <span class="fas fa-plus "></span>
                          </button>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-10">
                          <select bind:value={weekdaySelectedId} class="form-select" id="country" aria-label="Default select example">
                            <option selected>select weekday</option>
                            {#each $weekdays as weekday}
                              <option value={weekday.id}>{weekday.name}</option>
                            {/each}
                          </select>
                          <div class="input-group mt-2">
                            <span class="input-group-text"><span class="far fa-calendar-alt"></span></span>
                            <input bind:value={hour} data-datepicker="" class="form-control datepicker-input" id="birthday" type="time" step="1" placeholder="hh/mm/ss" required="">                                               
                          </div>
                        </div>
                        <div class="col-2">
                          <button on:click={addGroupWeekday} class="btn btn-primary mb-4 dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <span class="fas fa-plus "></span>
                          </button>
                        </div>
                      </div>
                    {/if}
									</div>
								</div>
								{#if group.groupWeekdayses && group.groupWeekdayses.length != 0 && $userWorker.fullAccess }
									<p class="mb-2">Weekdays</p>
									<div class="card border-light shadow-sm mb-4">
										<div class="card-body p-0">
												<div class="table-responsive">
														<table class="table table-centered table-nowrap mb-0 rounded">
																<thead class="thead-light">
																	<tr>
																		<th class="border-0">Actions</th>
																				<th class="border-0">Day</th>
																				<th class="border-0">Start</th>
																				<th class="border-0">Duration</th>
																		</tr>
																</thead>
																<tbody>
																	{#each group.groupWeekdayses as weekday, i}
																		<!-- Item -->
																		<tr>
																			<td class="border-0">
																				<button class="btn btn-danger" on:click={() => deleteDay(i, weekday.id)}>del</button>
																			</td>
																			<td class="border-0"><a class="text-primary font-weight-bold">{weekday.weekdays.name}</a> </td>
																				<td class="border-0">
																					{weekday.hour}	
																				</td>
																				<td class="border-0">
																					{weekday.duration} 
																				</td>
																		</tr>
																	{/each}
																</tbody>
														</table>
												</div>
										</div>
										</div>
								{/if}
								{#if group.groupCustomers && group.groupCustomers.length!= 0 && ($userWorker.canAddCustomersToGroup || $userWorker.fullAccess)}
									<p class="mb-2">Customers</p>
									<div class="card border-light shadow-sm mb-4">
										<div class="card-body p-0">
												<div class="table-responsive">
														<table class="table table-centered table-nowrap mb-0 rounded">
																<thead class="thead-light">
																	<tr>
																				<th class="border-0">Actions</th>
																				<th class="border-0">Name</th>
																				<th class="border-0">Last name</th>
																				<th class="border-0">Email</th>
																				<th class="border-0">Phone</th>
																		</tr>
																</thead>
																<tbody>
																	{#each group.groupCustomers as customer, i}
																		<!-- Item -->
																		<tr>
																			<td class="border-0">
																				<button class="btn btn-danger" on:click={() => removeCustomer(i, customer.id)}>del</button>
																			</td>
																			<td class="border-0"><a class="text-primary font-weight-bold">{customer.userCustomer.user.name}</a> </td>
																				<td class="border-0">
																					{customer.userCustomer.user.lastname}	
																				</td>
																				<td class="border-0">
																					{customer.userCustomer.user.email} 
																				</td>
																				<td class="border-0">
																					{customer.userCustomer.user.phone} 
																				</td>
																		</tr>
																	{/each}
																</tbody>
														</table>
												</div>
										</div>
										</div>
								{/if}
								{#if group.groupWorkers && group.groupWorkers.length != 0 && $userWorker.fullAccess}
									<p class="mb-2">Workers</p>
									<div class="card border-light shadow-sm mb-4">
										<div class="card-body p-0">
												<div class="table-responsive">
														<table class="table table-centered table-nowrap mb-0 rounded">
																<thead class="thead-light">
																	<tr>
																		<th class="border-0">Actions</th>
																				<th class="border-0">Name</th>
																				<th class="border-0">Last name</th>
																				<th class="border-0">Email</th>
																				<th class="border-0">Phone</th>
																		</tr>
																</thead>
																<tbody>
																	{#each group.groupWorkers as worker, i}
																		<!-- Item -->
																		<tr>
																			<td class="border-0">
																				<button class="btn btn-danger" on:click={() => removeWorker(i, worker.id)}>del</button>
																			</td>
																			<td class="border-0"><a class="text-primary font-weight-bold">{worker.userWorker.user.name}</a> </td>
																				<td class="border-0">
																					{worker.userWorker.user.lastname}	
																				</td>
																				<td class="border-0">
																					{worker.userWorker.user.email} 
																				</td>
																				<td class="border-0">
																					{worker.userWorker.user.phone} 
																				</td>
																		</tr>
																	{/each}
																</tbody>
														</table>
												</div>
										</div>
										</div>
								{/if}
									<div class="d-block d-sm-flex justify-content-center align-items-center mt-4">
										<button class="btn btn-primary" on:click={toggle}>Close</button>
									</div>
							</div>
					</div>
				</div>
			</div>
		</div>
	</div>
{/if}
{#if showAddGroup}
<div class="modal-backdrop fade show"></div>
<div class="modal fade show" id="modal-form" tabindex="-1" role="dialog" aria-labelledby="modal-form" aria-modal="true" style="display: block;">
		<div class="modal-dialog modal-dialog-centered" role="document">
				<div class="modal-content">
						<div class="modal-body p-0">
								<div class="card border-light p-4">
										<div class="card-header border-0 text-center pb-0">
											<h2 class="h4">Add group {enableCreateGroup}</h2>
										</div>
										<div class="card-body">
												<form action="#" class="mt-4">
														<!-- Form -->
														<div class="form-group mb-4">
																<label for="exampleInputEmailCard1">Name</label>
																<div class="input-group">
																	<input type="text" class="form-control" placeholder="name" id="exampleInputEmailCard1" bind:value={newGroup.name}>
																</div>  
														</div>
														<div class="form-group mb-4">
																<label for="exampleInputEmailCard1">Description</label>
																<div class="input-group">
																	<textarea type="text" class="form-control" placeholder="description" id="exampleInputEmailCard1" bind:value={newGroup.description}></textarea>
																</div>  
														</div>
														<div class="form-group mb-4">
																<label for="exampleInputEmailCard1">Max customers</label>
																<div class="input-group">
																	<input type="number" class="form-control" placeholder="max customers" id="exampleInputEmailCard1" bind:value={newGroup.maxCustomer}>
																</div>  
														</div>
														<div class="form-group mb-4">
																<label for="exampleInputEmailCard1">Start</label>
																<div class="input-group">
																	<input type="date" class="form-control" placeholder="start" id="exampleInputEmailCard1" bind:value={newGroup.start}>
																</div>  
														</div>
														<div class="d-block d-sm-flex justify-content-center align-items-center mt-4">
															<button class="btn btn-primary" on:click={addGroup} disabled={!enableCreateGroup}>Add</button>
															<button class="btn btn-primary ml-4" on:click={toggleAddGroup}>Close</button>
														</div>
												</form>
										</div>
								</div>
						</div>
				</div>
		</div>
</div>

{/if}
<div class="row p-0 mb-4">
	<div class="col-8">
		<div class="input-group input-group-merge search-bar p-0">
				<span class="input-group-text" id="topbar-addon"><span class="fas fa-search"></span></span>
				<input type="text" class="form-control" id="topbarInputIconLeft" placeholder="Search" aria-label="Search" aria-describedby="topbar-addon" bind:value={search}>
		</div>
	</div>
	{#if $userWorker.fullAccess}
		<div class="col-4">
			<button class="btn btn-primary" on:click={toggleAddGroup}>Add group</button>
		</div>
	{/if}
</div>
<div class="card border-light shadow-sm mb-4">
	<div class="card-body">
			<div class="table-responsive">
					<table class="table table-centered table-nowrap mb-0 rounded">
							<thead class="thead-light">
									<tr>
											<th class="border-0">#</th>
											<th class="border-0">Name</th>
											<th class="border-0">Description</th>
											<th class="border-0">Start</th>
											<th class="border-0">Max customers</th>
											<th class="border-0">Actions</th>
									</tr>
							</thead>
							<tbody>
								{#each _groups as group}
									<!-- Item -->
									<tr>
										<td class="border-0"><a href="#" class="text-primary font-weight-bold">{group.id}</a> </td>
											<td class="border-0 font-weight-bold"><span class="icon icon-xs icon-gray w-30"><span class="fas fa-globe-europe"></span></span>{group.name}</td>
											<td class="border-0">
												{group.description}	
											</td>
											<td class="border-0">
												{toLocalTime(group.start)} 
											</td>
											<td class="border-0">
												{group.maxCustomer}
											</td>
											<td class="border-0">
												<button class="btn btn-primary" on:click={() => selectGroup(group.id)}>view</button>
												<button class="btn btn-danger" on:click={() => deleteGroup(group.id)}>del</button>
											</td>
									</tr>
								{/each}
							</tbody>
					</table>
			</div>
	</div>
	</div>
